.containerShop{

    height: 70%;
    width: 90%;
}
.nav{
    
    display: flex;
position: absolute;
flex-wrap: wrap;
justify-content: flex-start;
align-items: center;
left: 3%;
top: 15%;
width: 30%;
height: 90%;
}
.nav h2{

  
    color:rgb(196, 162, 11);
    font-size: 50px;
   margin-top: 10%;
    text-align: start;
    width: 100%;
}
.nav ul{
    background-color:   rgba(173, 27, 202, 0.384);
    width: 75%;
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    padding:3%;
    height: 80%;
}
.nav ul>a,.nav ul>li{
    color: yellow;
    cursor: pointer;
    font-size: 30px;
    list-style-type: none;
    text-decoration: none;
}
.nav ul>a:hover,.nav ul>li:hover {
    color: rgb(215, 84, 233);
}

@media screen and (max-width:1200px) {
    .nav ul>a,.nav ul>li{
        color: yellow;
        cursor: pointer;
        font-size: 20px;
        list-style-type: none;
        text-decoration: none;
    }
    .nav{
        height: 60%;
    }
    
}
@media screen and (max-width:800px){
    .containerShop{
        display: flex;
    
        flex-wrap: wrap;
    }
    .nav{
        width: 100%;
        left: 10%;
    }
    .nav h2{
        text-align: center;
    }
    .nav ul{
        width: 100%;
        text-align: center;
    }
    
}